import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import 'rxjs/add/operator/map';
import { tokenNotExpired } from 'angular2-jwt';
@Injectable()
export class QuoteService {
  domain = '';
  authToken;
  user;
  options;
  constructor(
    private http: Http
  ) { }

  loadToken() {
    this.authToken = localStorage.getItem('token'); // Avoir le jeton et l'attribuer pour qu'il soit valable partout
  }
  createAuthentificationHeaders() {
    this.loadToken();
    this.options = new RequestOptions({
      headers: new Headers({
        'Content-Type': 'application/json', // Format JSON
        'authorization': this.authToken
      })
    });
  }

  storeUserData(token, user) {
    localStorage.setItem('token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  loggedIn() {
    return tokenNotExpired();
  }

  login(user) {
    return this.http.post(this.domain + 'user/loginAdmin', user).map(res => res.json());
    }


  logout() {
    this.authToken = null;
    this.user = null;
    localStorage.clear();
  }

  newQuote(quote) {
    this.createAuthentificationHeaders();
    return this.http.put(this.domain + 'user/newQuote', quote, this.options).map(res => res.json());

  }

  newUser(user) {
    return this.http.post(this.domain + 'user/register', user).map(res => res.json());
  }

  editUser(user) {
    this.createAuthentificationHeaders();
    return this.http.put(this.domain + 'user/updateUser', user, this.options).map(res => res.json());
  }

  deleteUser(user) {
    this.createAuthentificationHeaders();
    return this.http.delete(this.domain + 'user/deleteUser/' + user._id,this.options).map(res => res.json());;
  }

  getAllUsers() {
    return this.http.get(this.domain + 'user/allUsers').map(res => res.json());
  }

  newAdmin(admin) {
    this.createAuthentificationHeaders();
    return this.http.post(this.domain + 'user/registerAdmin', admin, this.options).map(res => res.json());
  }
  
  editAdmin(admin) {
    this.createAuthentificationHeaders();
    return this.http.put(this.domain + 'user/updateAdmin', admin, this.options).map(res => res.json());
  }

  deleteAdmin(admin) {
    this.createAuthentificationHeaders();
    return this.http.delete(this.domain + 'user/deleteAdmin/' + admin._id,this.options).map(res => res.json());;
  }

  getAllAdmins() {
    return this.http.get(this.domain + 'user/allAdmins').map(res => res.json());
  }


}



