import { Component, OnInit } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { QuoteService } from '../../services/quote.service';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {

  userToModify: any;
  message: any;
  messageClass: string;
  processing: boolean = false;
  selectedValue: any;
  users: any;
  form: FormGroup;
  edit: boolean = false;
  addUser: boolean = false;
  constructor(
    private formBuilder: FormBuilder,
    private quoteService: QuoteService
  ) {
    
   }


  createForm() {
    this.form = this.formBuilder.group({
      username: ['', Validators.required],
      email: ['', Validators.required],
      password: ['', Validators.required],
    });
  }

  disableForm() {
    this.form.controls['username'].disable();
    this.form.controls['email'].disable();
    this.form.controls['password'].disable();
  }


  enableForm() {
    this.form.controls['username'].enable();
    this.form.controls['email'].enable();
    this.form.controls['password'].enable();
  }


  newUserForm() {
    this.addUser = true;
    this.createForm();
  }
  cancel() {
    this.addUser = false;
    this.edit = false;
  }

  onAddSubmit() {
    this.processing = true;
    this.disableForm();
    let user = {
      username: this.form.get('username').value,
      email: this.form.get('email').value,
      password: this.form.get('password').value,
    }
    this.quoteService.newUser(user).subscribe((data) => {
      if (!data.success) {
        this.messageClass = 'alert alert-danger';
        this.message = data.message;
        this.processing = false;
        this.enableForm();
      }
      else {
        this.messageClass = 'alert alert-success';
        this.message = data.message;
        setTimeout(() => {
          this.form.reset();
          this.enableForm();
          this.getAllUsers();
          this.processing = false;
          this.message = null;
          this.messageClass = null;
          this.addUser = false;
        }, 1500);
      }
    });
  }

  modifyUser(user) {
    this.addUser = false;
    this.edit =true;
    this.userToModify = user;

  }

  updateUserSubmit() {
    this.quoteService.editUser(this.userToModify).subscribe( data => {
      if (!data.success) {
        this.messageClass = 'alert alert-danger';
        this.message = data.message;
      }else {
        this.messageClass = 'alert alert-success';
        this.message = data.message;
        setTimeout(() => {
          this.getAllUsers();
          this.message = null;
          this.messageClass = null;
          this.edit = false;
        }, 1500);

      }
    });
  }
  
  deleteUser(user) {
    this.quoteService.deleteUser(user).subscribe(data => {
      if (!data.success) {
        this.messageClass = 'alert alert-danger';
        this.message = data.message;
      }else {
        this.messageClass = 'alert alert-success';
        this.message = data.message;
        setTimeout(() => {
          this.getAllUsers();
          this.message = null;
          this.messageClass = null;
        }, 1500);
      }
    });
  }

  getAllUsers() {
    this.quoteService.getAllUsers().subscribe(data => {
      if (!data.success) {
        console.log(data.message);
      }
      else {
        console.log(data.users);
        this.users = data.users;
        this.selectedValue = null;
      }
    })
  }

  

  ngOnInit() {
    this.getAllUsers();
  }
}



