import { Component, OnInit } from '@angular/core';
import { QuoteService } from '../../services/quote.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-admin',
  templateUrl: './admin.component.html',
  styleUrls: ['./admin.component.css']
})
export class AdminComponent implements OnInit {

  adminToModify: any;
  message: any;
  messageClass: string;
  processing: boolean = false;
  selectedValue: any;
  admins: any;
  form: FormGroup;
  edit: boolean = false;
  addAdmin: boolean = false;
  constructor(
    private formBuilder: FormBuilder,
    private quoteService: QuoteService
  ) {
    
   }


  createForm() {
    this.form = this.formBuilder.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
    });
  }

  disableForm() {
    this.form.controls['username'].disable();
    this.form.controls['password'].disable();
  }


  enableForm() {
    this.form.controls['username'].enable();
    this.form.controls['password'].enable();
  }


  newAdminForm() {
    this.addAdmin = true;
    this.createForm();
  }
  cancel() {
    this.addAdmin = false;
    this.edit = false;
  }

  onAddSubmit() {
    this.processing = true;
    this.disableForm();
    let admin = {
      username: this.form.get('username').value,
      password: this.form.get('password').value,
    }
    this.quoteService.newAdmin(admin).subscribe((data) => {
      if (!data.success) {
        this.messageClass = 'alert alert-danger';
        this.message = data.message;
        this.processing = false;
        this.enableForm();
      }
      else {
        this.messageClass = 'alert alert-success';
        this.message = data.message;
        setTimeout(() => {
          this.form.reset();
          this.enableForm();
          this.getAllAdmins();
          this.processing = false;
          this.message = null;
          this.messageClass = null;
          this.addAdmin = false;
        }, 1500);
      }
    });
  }

  modifyAdmin(admin) {
    this.addAdmin = false;
    this.edit =true;
    this.adminToModify = admin;

  }

  updateAdminSubmit() {
    this.quoteService.editAdmin(this.adminToModify).subscribe( data => {
      if (!data.success) {
        this.messageClass = 'alert alert-danger';
        this.message = data.message;
      }else {
        this.messageClass = 'alert alert-success';
        this.message = data.message;
        setTimeout(() => {
          this.getAllAdmins();
          this.message = null;
          this.messageClass = null;
          this.edit = false;
        }, 1500);

      }
    });
  }
  
  deleteAdmin(admin) {
    this.quoteService.deleteAdmin(admin).subscribe(data => {
      if (!data.success) {
        this.messageClass = 'alert alert-danger';
        this.message = data.message;
      }else {
        this.messageClass = 'alert alert-success';
        this.message = data.message;
        setTimeout(() => {
          this.getAllAdmins();
          this.message = null;
          this.messageClass = null;
        }, 1500);
      }
    });
  }

  getAllAdmins() {
    this.quoteService.getAllAdmins().subscribe(data => {
      if (!data.success) {
      }
      else {
        this.admins = data.admins;
        this.selectedValue = null;
      }
    })
  }

  

  ngOnInit() {
    this.getAllAdmins();
  }

}



